	module.exports = {"data":{"markdownRemark":{"html":"<h1>ASP.NET MVC - Веб-феры</h1>\n<p>Веб-фермы это группы серверов, которые разделяют между собой обслуживание запросов. Обычно находятся за балансировщиком нагрузки. Теоретически возможно расположить представления на одном сервере, модель на другом. Один из главных недостатков заключается в том, что на сервере может не быть текущей сессии. Чтобы этого избежать, нужно использовать OutProc режимы управления сессиями. </p>","frontmatter":{"path":"/blog/mvc-web-farms","title":"ASP.NET MVC - Веб-феры"}}},"pathContext":{}}

